___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --electric-blue: #007bff;
  --light-electric-blue: #00bfff;
  --dark-blue: #0056b3;
  --light-gray: #f2f2f2;
  --text-icons: #ffffff;
  --soft-black: #333333;

  --sunny-yellow: #ffeb3b; /* Bright and sunny */
  --cloudy-gray: #9e9e9e; /* Overcast or cloudy */
  --rainy-blue: #607d8b; /* Rainy or drizzle */
  --stormy-dark-blue: #263238; /* Stormy or heavy rain */
  --snowy-white: #e0e0e0; /* Snowy or icy */
  --foggy-light-gray: #e0f7fa; /* Foggy or misty */
  --windy-light-blue: #81d4fa; /* Windy or breezy */
  --icy-blue: #b3e5fc;
  --moon-gray: #e0e0e0;

  --soft-white-blue: #f0faff;

  --search-bar-outline: 2.5px solid var(--soft-black);
}
button {
  border: none;
}

body.grid-layout {
  display: grid;
  grid-template-columns: min-content 1fr;
  height: 100vh;
}

/*Sidebar*/
/*#region*/
section.sidebar {
  grid-column: 1;
  background-color: var(--dark-blue);
  padding: 0.75em;
  box-shadow: 0 0 5px 2px var(--soft-black);
  width: min-content;
  color: var(--text-icons);

  display: grid;
  grid-template-rows: repeat(4, min-content);
  gap: 0.5em;
}

/*Logo*/
/*#region*/
.main-logo {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  align-items: center;
  grid-row: 1;
}
.logo-title {
  color: var(--text-icons);
  font-weight: bolder;
  font-size: 2rem;
}
.logo-icon {
  width: 3em;
}
.logo-icon .thunder-bolt {
  fill: var(--electric-blue);
  filter: drop-shadow(0 0 1px var(--light-electric-blue)) drop-shadow(0 0 1px var(--light-electric-blue));
}
.logo-icon .eye-sclera {
  fill: white;
}
.logo-icon .eye-pupil {
  fill: var(--soft-black);
}
.logo-icon .eye-iris {
  fill: var(--electric-blue);
}
/*#endregion*/

/*Sidebar utilities*/
/*#region*/
.sidebar-utilities {
  grid-row: 2;
  display: flex;
  align-items: stretch;
  justify-content: center;
  gap: 0.5em;
  border-top: 2px solid var(--windy-light-blue);
  padding-top: 0.25em;
  font-size: 1.25em;
}
.get-my-location-button {
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--soft-white-blue);
  border: 1px solid var(--windy-light-blue);
  border-radius: 10px;
  font-weight: bold;
}
.get-my-location-icon {
  width: 1.5em;
  fill: var(--electric-blue);
}
.get-my-location-button:hover {
  background-color: #cceeff;
  border-color: var(--windy-light-blue);
}
.get-my-location-button:hover > .get-my-location-icon {
  fill: var(--dark-blue);
}

.live-time {
  font-size: 1.75rem;
  text-align: center;
}
/*#endregion*/

/*Search bar*/
/*#region*/
.search-bar {
  grid-row: 3;
  min-width: min-content;
  display: flex;
  border: 2px solid var(--windy-light-blue);
  border-radius: 10px;
  overflow: hidden;
}
.search-bar > .search-input {
  flex: 1;
  border: none;
  width: 2em;
  background-color: var(--soft-white-blue);
  padding: 0.25em;
  outline: none;
}
.search-bar > .search-bar-button {
  border: none;
  padding: 0.1em;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--soft-white-blue);
}
.search-bar .search-bar-icon {
  width: 1.5em;
  fill: var(--electric-blue);
}
.search-bar > .search-bar-button:hover {
  background-color: #cceeff;
}
.search-bar > .search-bar-button:hover > .search-bar-icon {
  fill: var(--dark-blue);
}
/*#endregion*/

.options-container {
  grid-row: 4;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content min-content;
  gap: 0.25em;
}
.options-container-header {
  grid-row: 1;
  grid-column: 1/-1;
  text-align: center;
  font-size: 1.9rem;
  font-weight: bolder;
  border-top: 2px solid var(--windy-light-blue);
  padding-top: 0.25em;
}
.units-options {
  grid-column: 1;
  width: min-content;
  justify-self: center;
  text-align: center;
}
.ui-options {
  grid-column: 2;
  width: min-content;
  justify-self: center;
  text-align: center;
}
.ui-option {
  text-align: center;
}
.option-switches {
  display: flex;
  flex-direction: column;
  gap: 0.5em;
}

.option-header {
  border-bottom: 2px solid var(--windy-light-blue);
  margin-bottom: 0.25em;
}

/*Switches*/
/*#region*/
.switch > input[type='checkbox'] {
  opacity: 0;
  height: 0;
  width: 0;
}
.switch {
  display: flex;
  position: relative;
}
.switch-label {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  flex: 1;
}
.switch-label > .switch-options {
  display: flex;
  gap: 0.5em;
  justify-content: space-between;
}

.switch-slider:hover {
  box-shadow: 0 0 1px 1px var(--windy-light-blue);
}
.switch-label > .switch-slider {
  background-color: var(--soft-white-blue);
  height: 1.5em;
  border-radius: 15px;
  border: 3px solid var(--windy-light-blue);
}
.switch-label > .switch-slider::after {
  content: '';
  height: 1.25em;
  width: 50%;
  border-radius: 15px;
  position: absolute;
  left: 3%;
  background-color: var(--electric-blue);
  transition: 0.2s linear;
}
.switch > input[type='checkbox']:checked + .switch-label > .switch-slider::after {
  transform: translateX(90%);
}

.switch-active-option {
  color: var(--windy-light-blue);
}
/*#endregion*/

.saved-locations {
  display: flex;
  flex-direction: column;
  border-top: 2px solid var(--windy-light-blue);
  gap: 0.5em;
}
.saved-locations-header {
  font-size: 1.25rem;
  text-align: center;
  padding: 0.25em;
}

.saved-location {
  display: flex;
  justify-content: baseline;
  align-items: center;
  overflow: hidden;
  height: min-content;
}

.saved-location > button {
  background-color: var(--dark-blue);
  color: var(--soft-white-blue);
  font-size: 1.2rem;
}
.remove-saved-location {
  font-weight: bolder;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  border: 2px solid var(--windy-light-blue);
}
.remove-saved-location:hover {
  background-color: var(--soft-black);
  color: red;
  border-color: var(--soft-white-blue);
}
.load-saved-location {
  width: 100%;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  border: 2px solid var(--windy-light-blue);
  border-left: none;
  max-width: 8em;
  overflow: hidden;
  text-wrap: nowrap;
  text-overflow: ellipsis;
}
.load-saved-location:hover {
  background-color: var(--windy-light-blue);
  color: var(--dark-blue);
  border-color: var(--soft-white-blue);
}

/*API Info*/
/*#region*/
.weather-api-info {
  justify-self: first baseline;
  align-self: last baseline;
  font-weight: bold;
}
.api-img {
  background-color: var(--soft-white-blue);
}
/*#endregion*/

/*#endregion*/

main.main-container {
  grid-column: 2;
  display: grid;
  grid-template-rows: 1fr 1fr;
  background-color: var(--soft-white-blue);
  color: var(--soft-black);
  font-size: 1.25rem;
  padding: 1.5em;
}

section.current-weather {
  grid-row: 1;
  padding: 0.5em;
  display: grid;
  grid-template-rows: min-content 1fr;
  grid-template-columns: min-content min-content 1fr;
  justify-items: center;
  align-items: center;
  background-color: var(--soft-white-blue);
}

.current-weather-header {
  grid-row: 1;
  grid-column: 1/-1;
  display: flex;
  gap: 0.5em;
  justify-self: baseline;
}
.save-this-location-button {
  padding: 0;
  background-color: var(--soft-white-blue);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.save-this-location-icon {
  width: 2.5em;
  height: 2.5em;
  fill: none;
  stroke: var(--dark-blue);
}
.saved-location-icon,
.save-this-location-button:hover > .save-this-location-icon {
  fill: var(--dark-blue);
  stroke: var(--electric-blue);
}
.save-this-location-button:hover > .saved-location-icon {
  fill: var(--soft-black);
  stroke: red;
}
.location-info-container {
  display: flex;
  flex-direction: column;
  gap: 0;
}
.location-info-container > .location {
  font-size: 3rem;
  font-weight: bold;
}
.location-info-container > .last-updated {
  font-size: 0.75rem;
  font-style: italic;
}

.current-weather-primary {
  padding: 2em;
}
.current-weather-primary-header {
  grid-row: 1;
  grid-column: 1/-1;
  display: flex;
  justify-content: baseline;
  align-items: center;
}
.current-weather-description {
  font-size: 2rem;
  font-weight: bold;
}
.current-primary-temperatures {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.current-weather-temp {
  font-size: 5rem;
  font-weight: bolder;
}
.current-feels-like-text {
  font-size: 1.5rem;
  font-style: italic;
}
.current-feels-like-temp {
  font-size: 3rem;
  font-weight: bold;
}

.current-label {
  font-style: italic;
  display: flex;
  justify-content: space-between;
  gap: 1em;
}
.current-value {
  font-weight: bold;
  font-style: normal;
}
.current-weather-other {
  padding: 2em;
}
.current-weather-gif {
  width: 75%;
  max-height: 75%;
}
.gif-query {
  font-style: italic;
}
.search-query {
  font-weight: bold;
}
section.forecast {
  grid-row: 2;
  background-color: var(--soft-white-blue);
  display: grid;
  grid-template-rows: min-content min-content;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.5em;
  padding: 1em;
  min-width: max-content;
  justify-items: center;
}
.forecast-header {
  grid-row: 1;
  grid-column: 1 / -1;
  font-size: 2.25rem;
  font-weight: bold;
  justify-self: center;
  align-self: center;
}
.forecast-card {
  grid-row: 2;
  background-color: var(--windy-light-blue);
  display: flex;
  flex-direction: column;
  height: min-content;
  border: 3px solid var(--electric-blue);
  border-radius: 10px;
  min-width: 300px;
  max-width: max-content;
}
.forecast-card-header {
  display: flex;
  gap: 0.5rem;
  font-weight: bolder;
  padding: 0.25em;
  font-size: 1.25rem;
  justify-content: center;
}
.forecast-icon-description {
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  font-weight: bold;
  padding-right: 0.25em;
  width: fit-content;
}
.forecast-card-label-values {
  display: flex;
  flex-direction: column;
  width: max-content;
  justify-content: space-between;
}
.forecast-label {
  font-style: italic;
  display: flex;
  gap: 0.5em;
  padding-left: 0.5em;
  padding-right: 0.5em;
  justify-content: space-between;
}
.forecast-label:hover {
  background-color: var(--soft-white-blue);
}
.forecast-value {
  font-weight: bold;
}

.loader-dialog {
  background-color: transparent;
  border: none;
}
.loader {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  border: 25px solid var(--cloudy-gray);
  border-top-color: var(--dark-blue);
  animation: spin 2s linear infinite;
}
.loader-dialog::backdrop {
  background-color: rgba(0, 0, 0, 0.25);
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none;
}
#sunny {
  background-color: var(--sunny-yellow);
}
#cloudy {
  background-color: var(--cloudy-gray);
}
#rainy {
  background-color: var(--rainy-blue);
}
#stormy {
  background-color: var(--stormy-dark-blue);
}
#snowy {
  background-color: var(--snowy-white);
}
#foggy {
  background-color: var(--foggy-light-gray);
}
#windy {
  background-color: var(--windy-light-blue);
}
#cold {
  background-color: var(--icy-blue);
}
#moon {
  background-color: var(--moon-gray);
}
