00vh;\n}\n\n/*Sidebar*/\n/*#region*/\nsection.sidebar {\n  grid-column: 1;\n  background-color: var(--dark-blue);\n  padding: 0.75em;\n  box-shadow: 0 0 5px 2px var(--soft-black);\n  width: min-content;\n  color: var(--text-icons);\n\n  display: grid;\n  grid-template-rows: repeat(4, min-content);\n  gap: 0.5em;\n}\n\n/*Logo*/\n/*#region*/\n.main-logo {\n  display: flex;\n  flex-direction: row-reverse;\n  justify-content: flex-end;\n  align-items: center;\n  grid-row: 1;\n}\n.logo-title {\n  color: var(--text-icons);\n  font-weight: bolder;\n  font-size: 2rem;\n}\n.logo-icon {\n  width: 3em;\n}\n.logo-icon .thunder-bolt {\n  fill: var(--electric-blue);\n  filter: drop-shadow(0 0 1px var(--light-electric-blue)) drop-shadow(0 0 1px var(--light-electric-blue));\n}\n.logo-icon .eye-sclera {\n  fill: white;\n}\n.logo-icon .eye-pupil {\n  fill: var(--soft-black);\n}\n.logo-icon .eye-iris {\n  fill: var(--electric-blue);\n}\n/*#endregion*/\n\n/*Sidebar utilities*/\n/*#region*/\n.sidebar-utilities {\n  grid-row: 2;\n  display: flex;\n  align-items: stretch;\n  justify-content: center;\n  gap: 0.5em;\n  border-top: 2px solid var(--windy-light-blue);\n  padding-top: 0.25em;\n  font-size: 1.25em;\n}\n.get-my-location-button {\n  padding: 0;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  background-color: var(--soft-white-blue);\n  border: 1px solid var(--windy-light-blue);\n  border-radius: 10px;\n  font-weight: bold;\n}\n.get-my-location-icon {\n  width: 1.5em;\n  fill: var(--electric-blue);\n}\n.get-my-location-button:hover {\n  background-color: #cceeff;\n  border-color: var(--windy-light-blue);\n}\n.get-my-location-button:hover > .get-my-location-icon {\n  fill: var(--dark-blue);\n}\n\n.live-time {\n  font-size: 1.75rem;\n  text-align: center;\n}\n/*#endregion*/\n\n/*Search bar*/\n/*#region*/\n.search-bar {\n  grid-row: 3;\n  min-width: min-content;\n  display: flex;\n  border: 2px solid var(--windy-light-blue);\n  border-radius: 10px;\n  overflow